import React from 'react'

// WYSYŁANIE MAILi
// ZMIANA NAZWY MAILA
// DODAĆ MAPE
// Use ref zamiant getid

export default function Contact() {

    window.scrollTo(0, 0);

    var modal = document.getElementById("myModal");
    window.onclick = function (event) {
        if (event.target == modal) {
            modal.style.display = "none"
        }

    }

    function handleSubmit(e) {
        e.preventDefault();
    }

    return (
        <section className="section-contact">
            <h1>Kontakt</h1>
            <p>Poproś o bezpłatną wycenę</p>
            <div className="information">
                <div className="row-info">
                    <img src="img/phone-call(1).png" alt="" />
                    <p>510 346 634</p>
                </div>
                <div className="row-info">
                    <img src="img/mail.png" alt="" />
                    <p>kontakt@email.pl</p>
                </div>
                <div className="row-info">
                    <img src="img/location.png" alt="" />
                    <p>ul. Mroźna 556 Wrocław 34-650</p>
                </div>
                <div className="row-info">
                    <a href="https://www.facebook.com/Tapi-Masters-pranie-tapicerki-tel-570727895-100833112276572/"><img src="img/facebook.png" alt="" /></a>
                    <p>Odwiedź nas na facebooku:</p>
                </div>
            </div>
            <div className="contact-box">
                <div className="form-div">

                    <form onSubmit={handleSubmit}>
                        <input type="text" placeholder="imię" />
                        <input type="phone" placeholder="nr. tel" />
                        <input type="email" placeholder="e-mail" />
                        <textarea placeholder="W czym możemy pomóc?" cols="30" rows="10"></textarea>
                        <button onClick={() => {
                            modal.style.display = "block"
                        }} className="contact-button" type="submit">WYŚLIJ</button>
                    </form>

                </div>
                <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d80106.37698437918!2d17.10230249707032!3d51.13937089711898!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x89b7e69bbf6378ea!2sTAPI%20MASTERS%20-%20pranie%20i%20czyszczenie%20tapicerki%20samochodowej%20i%20meblowej%20.%20Pranie%20wyk%C5%82adzin%20i%20dywan%C3%B3w%20.%20Dojazd%20Wroc%C5%82aw%20i%20okolice%20za%20darmo!5e0!3m2!1spl!2spl!4v1628160736280!5m2!1spl!2spl" allowfullscreen="" loading="lazy"></iframe>
            </div>


            <div id="myModal" className="modal">
                <div className="modal-content">
                    <span onClick={() => { modal.style.display = "none" }} className="close">&times;</span>
                    <p>Wiadomość została wysłana</p>
                </div>
            </div>
        </section>
    )
}