import React from 'react'
import Aos from "aos"
import 'aos/dist/aos.css';
import { useEffect } from "react"

export default function ServiceTable(props){
    function generateTableRow(d, i) {
        return (
            <tr>
                <td><p>{d}</p></td>
                <td><p><strong>{props.price[i]}</strong></p></td>
            </tr>
        )
    }
    useEffect(() => {
        Aos.init({duration: 1000})
    }, [])

    return(
        <div className="pricing-box" data-aos="fade">
            <hr />
            <h3>{props.service}</h3>
            <table>
                <tbody>
                    {props.detail.map(generateTableRow)}
                </tbody>
            </table>
        </div>
    )
}