export default class CarouselLogic {
    constructor(transition, automate = true, interval = 3000, vertical = false) {
        this.vertical = vertical;
        this.carousel = document.querySelector(".carousel");
        if(this.carousel === null) return;

        // this.buttonNext = this.carousel.querySelector(".carousel-button-next");
        // this.buttonPrev = this.carousel.querySelector(".carousel-button-prev");

        // this.buttonNext.addEventListener("click", () => { this.handleNext(); });
        // this.buttonPrev.addEventListener("click", () => { this.handlePrev(); });

        this.carouselElements = this.carousel.querySelectorAll(".carousel-element");
        this.edgeCount = Math.floor(this.carouselElements.length / 2)
        this.elementsMap = this.generateElementsMap();

        // this.carousel.style.transform = (this.vertical) ? "rotate(90deg)" : null;
        // this.carousel.style.width     = (this.vertical) ? "1100px" : "100%";
        this.carouselElements.forEach((e, i) => {
            e.style.visibility = (
                this.elementsMap[i] >= this.edgeCount ||
                this.elementsMap[i] <= -this.edgeCount
            ) ? "hidden" : "visible";

            if (this.vertical) {
                e.style.transform = "translate(-50%, -50%) rotate(-90deg)";
                e.style.height = "30%";
                e.style.width = "90vw";
            }
        })

        this.transition = transition;
        this.activeElement = 0;

        if (automate) {
            setInterval(() => this.handleNext(), interval);
            this.carousel.addEventListener('mousein', () => this.pauseAutomate = true);
            this.carousel.addEventListener('mouseout', () => this.pauseAutomate = false);
        }

        this.ignore = false;
        this.pauseAutomate = false;
        this.shiftElements();
        setTimeout(() => { this.carousel.style.opacity = "1"; }, this.transition * 1000);
    }

    generateElementsMap() {
        let elementsMap = [];
        for (let i = -this.edgeCount; i <= this.edgeCount; i++) {
            elementsMap.push(i);
        }
        return elementsMap;
    }

    handlePrev() {
        if (this.ignore || this.pauseAutomate) return;

        this.carouselElements.forEach((e, i) => {
            this.elementsMap[i]++;
            e.style.visibility = (this.elementsMap[i] >= this.edgeCount) ? "hidden" : "visible"
            this.elementsMap[i] = (this.elementsMap[i] > this.edgeCount) ? -this.edgeCount : this.elementsMap[i]
        })

        this.shiftElements();
    }

    handleNext() {
        if (this.ignore || this.pauseAutomate) return;

        this.carouselElements.forEach((e, i) => {
            this.elementsMap[i]--;
            e.style.visibility = (this.elementsMap[i] <= -this.edgeCount) ? "hidden" : "visible"
            this.elementsMap[i] = (this.elementsMap[i] < -this.edgeCount) ? this.edgeCount : this.elementsMap[i]
        })

        this.shiftElements();
    }

    shiftElements() {
        this.carouselElements.forEach((e, i) => {
            e.style.transition = this.transition + "s";

            if (this.elementsMap[i] <= -2) e.style.left = "-101%";
            else if (this.elementsMap[i] === -1) e.style.left = "17.5%";
            else if (this.elementsMap[i] === 0) e.style.left = "50%";
            else if (this.elementsMap[i] === 1) e.style.left = "82.5%";
            else if (this.elementsMap[i] >= 2) e.style.left = "200%";

            this.ignore = true;
            setTimeout(() => { this.ignore = false; }, this.transition * 1000);
        })
    }
}
