import React, { useEffect } from 'react'
import CarouselItem from './CarouselItem'
import CarouselLogic from './carouselLogic'

export default function Carousel(props) {

    useEffect(() => {
        console.log('Creating carousel');
        new CarouselLogic(1, true, 4000, window.innerWidth < 650);
    })

    let carouselItems = []

    while(carouselItems.length < 5) {
        carouselItems = carouselItems.concat(props.itemsList)
    }

    if(carouselItems.length % 2 === 0) carouselItems.push(props.itemsList[0])

    return (
        <div className="carousel">
            {carouselItems.map(item => <CarouselItem 
                name={item.name}
                stars={item.stars}
                description={item.description}
            />)}
        </div>
    )
}
