import React from 'react'

export default function CarouselItem(props) {
    return (
        <div className="carousel-element">
            <h3 className="carousel-element-title">{props.name}</h3>
            <img className="carousel-element-stars" src={props.stars} alt="" />
            <p className="carousel-element-description">{props.description}</p>
        </div>
    )
}
