import Navbar from './components/Navbar';
import { BrowserRouter as Router, Switch, Route, BrowserRouter } from 'react-router-dom';
import Home from "./components/Home/Home"
import About from "./components/About/About"
import Pricing from './components/Pricing/Pricing';
import Galery from './components/Galery/Galery';
import Ofert from './components/Ofert/Ofert';
import Sales from './components/Sales/Sales';
import Contact from './components/Contact/Contact';
import ForBuissnes from './components/ForCompanies/ForBuissnes';
import Footer from './components/Footer';


function App() {
  return (
      <BrowserRouter>
    <div className="App">
      <Navbar />
      <Route path="/Tapi-Masters/" exact={true} component={Home} />
      <Route path="/Tapi-Masters/o_nas" exact={true} component={About} />
      <Route path="/Tapi-Masters/cennik" exact={true} component={Pricing} />
      <Route path="/Tapi-Masters/realizacje" exact={true} component={Galery} />
      <Route path="/Tapi-Masters/oferta" exact={true} component={Ofert} />
      <Route path="/Tapi-Masters/promocje" exact={true} component={Sales} />
      <Route path="/Tapi-Masters/kontakt" exact={true} component={Contact} />
      <Route path="/Tapi-Masters/dla_firm" exact={true} component={ForBuissnes} />
      <Footer />
    </div>
    </BrowserRouter>
  );
}

export default App;
