import React from 'react'
import ServiceTable from './ServiceTable'
import PricingDB from './PricingDB'

// Ułożyć ładnie
// Zrobić css
// baza danych

export default function Pricing() {
    window.scrollTo(0, 0);
    return (
        <section>
            <h1>Cennik</h1>
            <p>Zapoznaj się z naszymi konkurencyjnymi cenami. Pamiętaj, że dojazd do Wrocławia i okolic jest darmowy</p>
            <div className="pricing-boxes">
                {PricingDB.map((serviceTable) => {
                    return (
                        <ServiceTable
                            service={serviceTable.service}
                            detail={serviceTable.detail}
                            price={serviceTable.price}
                        />
                    )
                })}
            </div>
        </section>
    )
}