import React, { useState } from 'react'
import { withRouter, Link } from 'react-router-dom'


function Navbar() {

    const [burgerActive, setBurgerActive] = useState(false);

    const handleClick = (e) => {
        e.stopPropagation();
        setBurgerActive((prevState) => {
            return (!prevState)
        })
    }

    window.addEventListener("click", () => {
        setBurgerActive(false);
    });

    return (
        <section>
            <div className="navbar">
                <img className="logo-img" src="img/TAPI.png" alt="" />
                <Link className="nav-link" to="/"><h1 className="logo-text">Tapi Masters</h1></Link>
                <div className={"nav-links" + ((burgerActive) ? " nav-active" : "")}>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/">Strona główna </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/o_nas">O nas </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/cennik">Cennik </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/realizacje">Realizacje </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/oferta">Oferta </Link>
                    <Link onClick={handleClick} className="nav-link blue-color" to="/Tapi-Masters/promocje"><b>Promocje</b> </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/kontakt">Kontakt </Link>
                    <Link onClick={handleClick} className="nav-link" to="/Tapi-Masters/dla_firm">Dla firm </Link>
                </div>
                <div className="nav-icons">
                    <a href="tel:510346634"><img src="img/phone-call(1).png" alt="phone" /></a>
                    <Link to="/kontakt"><img src="img/mail.png" alt="" /></Link>
                    <a href="https://www.facebook.com/Tapi-Masters-pranie-tapicerki-tel-570727895-100833112276572/"><img src="img/messenger.png" alt="" /></a>
                </div>
                <div onClick={handleClick} className="burger">
                    <img  src="img/menu.png" alt="" />
                </div>
            </div>
        </section>
    )
}

export default withRouter(Navbar);
